<template>
  <div>
    <div class="search">
      <input v-model="name_input" id="search_input" @input="search" placeholder="请输入客户名称"/>
      <!-- <div id="btn_search" @click="search">查询</div> -->
    </div>
    <div id="hint" :class="hint_class">{{hint}}</div>
    <div id="show_box" :class="show_class">
      <div class="item">
        <div class="title">
          统一客户名称
        </div>
        <div id="unify_name" class="content">
          {{unify_name}}
        </div>
      </div>
      <!-- <div class="item">
        <div class="title">
          客户名称
        </div>
        <div id="name" class="content">
          {{name}}
        </div>
      </div> -->
      <div class="item half">
        <div class="title">
          等级
        </div>
        <div id="level" class="content">
          {{level}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          标记
        </div>
        <div id="mark" class="content">
          {{mark}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          网络经理
        </div>
        <div id="N_manager" class="content">
          {{N_manager}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          网络经理电话
        </div>
        <div id="NM_phone" class="content">
          {{NM_phone}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          客户经理
        </div>
        <div id="C_manager" class="content">
          {{C_manager}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          客户经理电话
        </div>
        <div id="CM_phone" class="content">
          {{CM_phone}}
        </div>
      </div>
      <div class="item">
        <div id="btn_confirm" class="btn primary" @click="edit_customer">
          编辑
        </div>
      </div>
    </div>
    <ul id="customer_list" :class="show_class">
      <li class="list_item title_bar">
        <span class="title long">
          统一名称
        </span>
        <span class="title long">
          客户名称
        </span>
        <span class="title">
          标记
        </span>
        <span class="title">
          等级
        </span>
        <span class="title">
          网络经理
        </span>
        <span class="title">
          网络经理电话
        </span>
        <span class="title">
          客户经理
        </span>
        <span class="title">
          客户经理电话
        </span>
        <span class="title">
          来源
        </span>
        <span class="title">
          备注
        </span>
      </li>
      <li :id="index" class="list_item" v-for="(item,index) in customer_list" v-bind:key="item.id" @mousedown="count_start" @click="count_end">
          <span class="content long">
            {{item.unify_name}}
          </span>
          <span class="content long">
            {{item.name}}
          </span>
          <span class="content">
            {{item.mark}}
          </span>
          <span class="content">
            {{item.level}}
          </span>
          <span class="content">
            {{item.N_manager}}
          </span>
          <span class="content">
            {{item.NM_phone}}
          </span>
          <span class="content">
            {{item.C_manager}}
          </span>
          <span class="content">
            {{item.CM_phone}}
          </span>
          <span class="content">
            {{item.origin}}
          </span>
          <span class="content">
            {{item.remark}}
          </span>
      </li>
    </ul>
    <div id="button_group" :class="btn_class">
      <img class="btn" src="../assets/add.png" @click="new_customer"/>
      <img class="btn" src="../assets/import.png" @click="import_customer"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Search',
  props: {
  },
  data:function(){
    return {
      show_class:'hidden',
      hint_class:'',
      hint:'暂无相关记录',
      name_input:'',
      unify_name:'',
      name:'',
      level:'',
      mark:'',
      N_manager:'',
      NM_phone:'',
      C_manager:'',
      CM_phone:'',
      customer_list:[],
      counter:null,
      btn_class:''
    }
  },
  created:function(){
    this.get_data();
  },
  methods:{
    count_start:function(e){
      let self = this;
      if(self.counter != null){
        clearTimeout(self.counter)
      }
      self.counter = setTimeout(function(){
        self.onLongClick(e);
      },1500);
    },
    count_end:function(e){
      let self = this;
      if(self.counter != null){
        clearTimeout(self.counter)
        self.counter = null;
      }
      self.onClick(e);
    },
    onLongClick:function(e){
      let self = this;
      if(confirm("确认要删除这一条记录?")){
        let data = new FormData();
        let id = (self.customer_list[e.target.id]).id;
        data.append('customer_id',id);
        this.axios
          .post('http://r23y847803.zicp.vip/scripts/delete_customer_by_id.php',data)
          .then(function(res){
            if(res.data.status == 'success'){
              alert("删除成功")
              self.search();
            }else{
              alert(res.data.errMsg);
            }
          })
          .catch(function(err){
            alert(err);
          })
      }
    },
    onClick:function(e){
      //不建议直接修改单条记录,该功能暂时封印
      // let self = this;
      // self.counter = null;
      // self.$router.push({
      //   name:'add',
      //   params:{
      //     post:self.customer_list[e.target.id],
      //     search:self.name_input
      //   }
      // });
    },
    new_customer:function(){
      this.$router.push({
        name:'add',
        params:{}
      });
    },
    edit_customer:function(){
      let self = this;
      let post = {};
      post.unify_name = self.unify_name;
      post.name = self.name_input;
      post.level = self.level;
      post.N_manager = self.N_manager;
      post.NM_phone = self.NM_phone;
      post.C_manager = self.C_manager;
      post.CM_phone = self.CM_phone;
      self.$router.push({
        name:'add',
        params:{
          post:post,
          search:self.name_input
        }
      });
    },
    import_customer:function(){
      alert('导入功能暂未完工，敬请期待！');
    },
    search:function(){
      let self = this;
      if(this.name_input == ''){
        self.show_class = 'hidden';
        self.hint_class = '';
        self.hint = '暂无相关记录';
        self.btn_class = '';
        return;
      }
      self.hint = '加载中...';
      self.btn_class = 'hidden';

      let data = new FormData();
      data.append('name',this.name_input);
      this.axios
        .post('http://r23y847803.zicp.vip/scripts/get_customer_list.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            let list =  res.data.result;
            if(list.length == 0){
              self.show_class = 'hidden';
              self.hint_class = '';
              self.hint = '暂无相关记录';
              self.btn_class = '';
              return;
            }
            self.customer_list = list;
            self.show_class = "";
            self.btn_class = 'hidden';
            self.hint_class = 'hidden';
            let mark_list={};
            self.unify_name = '';
            self.level = '';
            self.N_manager = '';
            self.NM_phone = '';
            self.C_manager = '';
            self.CM_phone = '';
            for(let x in list){
              let record = list[x];
              if(self.unify_name == "" && record.unify_name != ""){
                self.unify_name = record.unify_name;
              }
              if(self.level == "" && record.level != ""){
                self.level = record.level;
              }
              if(self.N_manager == "" && record.N_manager != ""){
                self.N_manager = record.N_manager;
              }
              if(self.NM_phone == "" && record.NM_phone != ""){
                self.NM_phone = record.NM_phone;
              }
              if(self.C_manager == "" && record.C_manager != ""){
                self.C_manager = record.C_manager;
              }
              if(self.CM_phone == "" && record.CM_phone != ""){
                self.CM_phone = record.CM_phone;
              }
              if(record.mark.split(" ").length > 1){
                if(record.mark.split(" ")[0] == "SET" || record.mark.split(" ")[0] == "set"){
                  if(mark_list[record.mark.split(" ")[1]] == undefined){
                    mark_list[record.mark.split(" ")[1]] = 1;
                  }else{
                    mark_list[record.mark.split(" ")[1]] = mark_list[record.mark.split(" ")[1]] + 1;
                  }
                }
                if(record.mark.split(" ")[0] == "UNSET" || record.mark.split(" ")[0] == "unset"){
                  if(mark_list[record.mark.split(" ")[1]] == undefined){
                    mark_list[record.mark.split(" ")[1]] = -1;
                  }else{
                    mark_list[record.mark.split(" ")[1]] = mark_list[record.mark.split(" ")[1]] - 1;
                  }
                }
              }
            }
            self.mark = '';
            for(let mark in mark_list){
              if(mark_list[mark] > 0){
                self.mark = self.mark + mark;
              }
            }
          }else{
            alert(res.data.errMsg);
          }
        })
        .catch(function(err){
          alert(err)
        })
    },
    get_data:function(){
      if(this.$route.params.search != null){
        this.name_input = this.$route.params.search;
        this.search()
      }
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
