  width: 80vw;
  height: 5vh;
  line-height: 5vh;
  font-size: 2vh;
  padding-left: 1vw;
  padding-right: 1vw; 
  background: #FFFFFF;
  border-radius: 2vh;
  -webkit-appearance:button-bevel;
  transition: box-shadow 0.5s;
  box-shadow: -1px 1px 5px 0px #AAAAAA;
  /*-webkit-border-top-left-radius:2vw;
  -webkit-border-bottom-left-radius:2vw;*/
